import displayItem from '@/components/display-item.vue';
export default {
	components: { displayItem },
	data() {
		return {
			login: false,
			headerUrl: '/static/images/account/person-bg.jpg',
			uerInfo: {}
		}
	},
	methods: {
		goLogin() {
			if (!this.login) {
				uni.navigateTo({
					url: '/pages/account/Login'
				});
			}
		},
		goBalance() {
			console.log('goBalance');
		},
		goAbout() {
			uni.navigateTo({
				url: '/pages/about/about'
			});
		},
		
		goSetting() {
			uni.navigateTo({
				url: '/pages/account/Setting'
			});
		},
		onPress(type) {
			switch (type) {
				case 0:	// 个人信息
				break;
				case 1:	// 账户
				break;
				case 2:	// 已购试卷
				break;
				case 3:	// 礼券
				break;
				case 4:
				case 8:
					uni.showToast({
						title: '程序小哥正在快马加鞭，敬请期待噢~',
						duration: 2000
					});
					break;
				case 5:	// 笔记
				break;
				case 6:	// 留言
				break;
				case 7: // 收藏
				break;
				case 9:	// 做题记录
				break;
				case 10:	// 错题库
				break;
				
			}
		}
	}
}
